.main-content {
    width: 700px;
    height: 300px;
    background-color: rgb(109, 58, 58);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    .main-content__text {
        padding: 25px;
        p {
            color:  black;
            text-align: center;
            font-size: 23px;
            font-style: normal;
            font-weight: 600;
            line-height: 25px;
            letter-spacing: 0em;
        }
    }
    .main-content__button {
        button {
            width: 250px;
            height: 65px;
            border-radius: 30px;
            background-color: rgb(177, 19, 19);
            color:  black;
            text-align: center;
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 25px;
            letter-spacing: 0em;
            cursor: pointer;
            &:hover {
                background-color: rgb(156, 42, 42);
            }
        }
    }
}